@media screen and (min-width: 768px) {
  .galleryMuiNe {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(40, 8vh);
    grid-gap: 0.5rem;
  }

  .caption {
    position: relative;
    border-top: 0px;
    background: rgba(1, 1, 1, 0.1);
    min-height: 10px;
    padding: 8px 25px;
  }
  .galleryMuiNe__item {
    margin: 0;
  }

  .galleryMuiNe__img {
    width: 100%;
    height: calc(100% - 40px);
    object-fit: cover;
    display: block;
  }

  .galleryMuiNe__item--1 {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  .galleryMuiNe__item--2 {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 1;
    grid-row-end: 4;
