}
redContent {
  color: red;
  font-weight: bold;
}

tickContent {
  display: block;
  overflow-wrap: break-word;
}

tickContent:before {
  content: "\f00c";
  font-family: "FontAwesome";
  font-style: normal;
  font-weight: normal;
  margin-right: 8px;
  color: #000;
  font-size: 20px;
  white-space: pre;
}
