.grid-row {
  position: relative;
  width: auto;
  margin-left: -1.5%;
  margin-right: -1.5%;
}
.grid-row .grid-column {
  float: left;
  margin: 0.5rem 1.5%;
  min-height: 0.125rem;
}
.grid-row::after {
  content: "";
  display: table;
  clear: both;
}

