* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

body {
  font-style: normal;
  font-weight: lighter;
  font-size: 18px;
  line-height: 1.89;
  font-family: "Quicksand" !important;
  letter-spacing: 0em;
  color: #888;
  margin: 0;
  padding: 0;
  background-color: #fff;
  overflow-x: hidden;
  box-sizing: border-box;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

img {
  margin: 0;
  padding: 0;
  border: none;
}

.bypostauthor {
  margin: 0;
  padding: 0;
}

section img,
img {
  width: 100%;
