  top: 0;
  left: 0;
  width: 5px;
  bottom: 0;
  background-color: #dda668 !important;
}

blockquote.align-right {
  padding: 10px 40px 20px 20px;
  text-align: right;
  font-weight: 400;
}

blockquote.align-right:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 5px;
  bottom: 0;
  background-color: #dda668 !important;
}

hr,
.hr {
