}

.featture-box-cont a {
  text-transform: uppercase;
  font-weight: 700;
}

.feature-box:hover .feature-box-thumb img {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.feature-box:hover .featture-box-cont {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
