}

.image_info_thumb {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.image_info_thumb img {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

