.item-dirt-hov {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: block;
  background: rgba(255, 255, 255, 0.9);
  opacity: 0;
  transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
}

.item-dirt-hov span::before,
.item-dirt-hov span::after {
  content: "";
  position: absolute;
  transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  background-color: #272b2b;
  opacity: 0.2;
}

.item-dirt-hov span:nth-child(1)::before,
.item-dirt-hov span:nth-child(1)::after {
  top: 50%;
  margin-top: -1px;
  width: 50%;
  height: 2px;
}

.item-dirt-hov span:nth-child(1)::before {
  right: 50%;
}

.item-dirt-hov span:nth-child(1)::after {
  left: 50%;
}

.item-dirt-hov span:nth-child(2)::before,
.item-dirt-hov span:nth-child(2)::after {
  left: 50%;
  margin-left: -1px;
  width: 2px;
  height: 50%;
}

.item-dirt-hov span:nth-child(2)::before {
  bottom: 50%;
}

.item-dirt-hov span:nth-child(2)::after {
  top: 50%;
}

.item-dirt:hover .item-dirt-hov {
  opacity: 1;
}

.item-dirt:hover .item-dirt-hov span:nth-child(1)::before,
.item-dirt:hover .item-dirt-hov span:nth-child(1)::after {
  width: 25px;
}

.item-dirt:hover .item-dirt-hov span:nth-child(2)::before,
.item-dirt:hover .item-dirt-hov span:nth-child(2)::after {
